function aboutSlider() {
    var swiper = new Swiper('.about .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 0,
        navigation: {
            nextEl: '.about .swiper-button-next',
            prevEl: '.about .swiper-button-prev',
        },
        breakpoints: {
            300: {
                slidesPerView: "auto",
            },
            992: {
                slidesPerView: 3,
            },
        }
    })
}

aboutSlider()
